/* Navigation.css */
@import "../assets/colors/colors.css";
.navigation {
    background-color: var(--color-white); /* Màu xanh lá */
    padding: 20px;
    float: right;
  }
  
  .navigation ul {
    list-style-type: none;
    padding: 0;
    display: flex;
  }
  
  .navigation li {
    margin-right: 20px;
  }
  
  .navigation a {
    padding: 20px;
    text-decoration: none;
    color: var(--color-black);; /* Màu chữ trắng */
    font-weight: bold;
  }
  